<template>
    <div class="nav">
        <div class="logoBox">
            <div class="searchBox">
                <div class="optionBox">
                    <span v-for="(x,index) in option" v-bind:class="{'border_right_1':index<option.length-1}">{{x.text}}</span>
                    <div class="inputBox">
                        <input type="text">
                        <span></span>
                    </div>
                    
                </div>
            </div>
        </div>
        <div class="navBox">
            <span v-for="(x,index) in nav" v-bind:class="{'border_right_1':index<nav.length-1, 'color_F40':navIndex==index }" >
                <a v-bind:href="x.Url">{{x.text}}</a>
            </span>
        </div>
    </div>
</template>
<script>
    export default{
        name: 'nav',
        data (){
            return {
                navIndex:0,
                nav:[
                    {
                        text:'产品',
                        Url:'/'
                    },
                    {
                        text:'品牌',
                        Url:'/..'
                    },
                    {
                        text:'特卖',
                        Url:'/..'
                    },
                    {
                        text:'心得',
                        Url:'/..'
                    },
                    {
                        text:'事件',
                        Url:'/..'
                    },
                    {
                        text:'医生之家',
                        Url:'/..'
                    },
                    {
                        text:'客服',
                        Url:'/..'
                    }
                ],
                option:[
                    {
                        text:'登录',
                        Url:''
                    },
                    {
                        text:'加入会员',
                        Url:''
                    },
                    {
                        text:'我的',
                        Url:''
                    },
                    {
                        text:'购物车',
                        Url:''
                    }
                ]
            }
        },
        mounted(){
            for(var i=0;i<this.nav.length;i++){
                if(window.location.pathname == this.nav[i].Url){
                    
                }
            }
            
        }
    }
</script>
<style scoped>
    .nav{
        margin: auto;
        width: 1100px;
        height: 160px;
        overflow: hidden;
    }
    .logoBox{
        width: 100%;
        height: 110px;
        padding-top: 0px;
        background: url('../assets/logo_qianshoula.png') no-repeat center;
        background-position-y: 34px;
    }
    
    .searchBox{
        float: right;
    }
    .optionBox{
        float: right;
        width: 210px;
        overflow: hidden;
        padding-top: 20px;
    }
    .optionBox span{
        height: 14px;
        line-height: 14px;
        font-size: 14px;
        padding: 0px 6px;
    }
    .border_right_1{
        border-right: 1px solid #000;
    }
    .inputBox{
        margin-top: 12px;
        background-color: #ff4400;
        width: 100%;
        height: 28px;
    }
    .inputBox input{
        height: 26px;
        margin-top: 1px;
        margin-left: 1px;
        width:183px;
        text-indent: 1em;
    }
    .inputBox span{
       
        padding: 0px;
        float: right;
        width: 24px;
        height: 26px;
        background: url('../assets/option_img.png') no-repeat center;
        
    }
    .navBox{
        height: 40px;
        line-height: 40px;
        width: 100%;
    }
    .navBox span {
        font-size: 18px;
        font-weight: bold;
        padding: 0 54px;
    }
    .navBox a{
        text-decoration: none;
        color: black
    }
    .navBox .color_F40 a{
        color:#ff4400;
    }
    
</style>


